/* src/app/footer/footer.scss */
footer {
  background-color: #fff8e1;
  font-family: "Roboto", sans-serif;
}
h5 {
  color: #d97706;
}
.text-success,
a.text-success {
  color: #15803d !important;
  transition: color 0.3s ease;
}
a.text-success:hover {
  color: #14532d !important;
}
.dropdown-toggle {
  background: none;
  border: none;
  color: #15803d !important;
  font-size: 1rem;
  padding: 0;
}
.dropdown-toggle:hover {
  color: #14532d !important;
}
.dropdown-menu {
  background-color: #fff8e1;
  border: 1px solid #d97706;
}
.dropdown-menu.show {
  display: block;
}
.dropdown-item {
  color: #15803d !important;
}
.dropdown-item:hover {
  color: #14532d !important;
  background-color: #fef3c7;
}
.active-route {
  color: #d97706 !important;
  font-weight: bold;
  background-color: transparent;
}
.basket-icon {
  display: inline-flex;
  align-items: center;
}
.panier {
  height: 20px;
  width: 20px;
  background-color: #fef3c7;
  border-radius: 50%;
  border: 2px solid #d97706;
  transition: background-color 0.3s ease, border-color 0.3s;
}
.panier path {
  stroke: #d97706;
}
.btn-outline-warning {
  border-color: #d97706;
  color: #d97706;
}
.btn-outline-warning:hover {
  background-color: #d97706;
  color: #fff8e1;
}
@media (max-width: 767px) {
  .dropdown-menu {
    background-color: #fef3c7;
    border: none;
  }
}
/*# sourceMappingURL=footer.css.map */
